import {createRouter, createWebHistory} from "vue-router";
import Home from "../views/Home.vue";
const marketFlow=[
    {
        path:'/trafficProfile',
        name:'trafficProfile',
        meta:{
            title:'流量概况'
        },
        component:()=>import(
            '../views/marketFlow/TrafficProfile.vue'
        )
    },
    {
        path:'/passengerFlowDetails',
        name:'passengerFlowDetails',
        meta:{
            title:'客流明细'
        },
        component:()=>import(
            '../views/marketFlow/PassengerFlowDetails.vue'
        )
    },
    {
        path:'/trafficRanking',
        name:'trafficRanking',
        meta:{
            title:'流量排行'
        },
        component:()=>import(
            '../views/marketFlow/TrafficRanking.vue'
        )
    },
    {
        path:'/flowComparison',
        name:'flowComparison',
        meta:{
            title:'流量对比'
        },
        component:()=>import(
            '../views/marketFlow/FlowComparison.vue'
        )
    },
    {
        path:'/holidayRanking',
        name:'holidayRanking',
        meta:{
            title:'节假日分析'
        },
        component:()=>import(
            '../views/marketFlow/HolidayRanking.vue'
        )
    }
]
const shopPassengerFlow=[
    {
        path:'/passengerFlowAnalysis',
        name:'passengerFlowAnalysis',
        meta:{
            title:'客流分析'
        },
        component:()=>import(
            '../views/shopPassengerFlow/PassengerFlowAnalysis.vue'
        )
    },
    {
        path:'/brandPopularity',
        name:'brandPopularity',
        meta:{
            title:'品牌人气'
        },
        component:()=>import(
            '../views/shopPassengerFlow/BrandPopularity.vue'
        )
    },
    {
        path:'/storeAssociation',
        name:'storeAssociation',
        meta:{
            title:'店铺关联'
        },
        component:()=>import(
            '../views/shopPassengerFlow/StoreAssociation.vue'
        )
    },
    {
        path:'/shopByShop',
        name:'shopByShop',
        meta:{
            title:'店铺过店'
        },
        component:()=>import(
            '../views/shopPassengerFlow/ShopByShop.vue'
        )
    }
]
const marketManagement = [
    {
        path: '/locationInformation',
        name: 'locationInformation',
        meta: {
            title: '场所信息'
        },
        component: () => import(
            '../views/marketManagement/localelnfo.vue'
        )
    },
    {
        path: '/marketInformation',
        name: 'marketInformation',
        meta: {
            title: '商场信息'
        },
        component: () => import(
            '../views/marketManagement/marketInfo.vue'
        )
    },
]
const systemManagement=[
    {
        path:'/userManagement',
        name:'userManagement',
        meta:{
            title:'用户管理'
        },
        component: () => import (
            "../views/systemManagement/userManagement.vue")
    },
    {
        path:'/roleManagement',
        name:'roleManagement',
        meta:{
            title:'角色管理'
        },
        component: () => import (
            "../views/systemManagement/roleManagement.vue")
    },
    {
        path:'/personalInformation',
        name:'personalInformation',
        meta:{
            title:'个人信息'
        },
        component: () => import (
            "../views/systemManagement/personalInformation.vue")
    },
    {
        path:'/reportAccessStatistics',
        name:'reportAccessStatistics',
        meta:{
            title:'用户报表统计'
        },
        component: () => import (
            "../views/systemManagement/reportAccessStatistics.vue")
    },
    {
        path:'/userActionLog',
        name:'userActionLog',
        meta:{
            title:'用户操作日志'
        },
        component: () => import (
            "../views/systemManagement/userActionLog.vue")
    },
    {
        path:'/dataExportLog',
        name:'dataExportLog',
        meta:{
            title:'数据导出日志'
        },
        component: () => import (
            "../views/systemManagement/dataExportLog.vue")
    }
]
const routes = [
    {
        path: '/',
        redirect: '/home'
    }, 
    {
        path: "/",
        name: "Home",
        component: Home,
        children: [
            {
                path:'/home',
                name:'home',
                meta:{
                    title:'首页'
                },
                component:()=>import (
                    '../views/BodyHome.vue'
                )
            },
            {
                path:'/passengerFlow',
                name:'passengerFlow',
                meta:{
                    title:'客流量'
                },
                component: () => import (
                    /* webpackChunkName: "dashboard" */
                    "../views/realtimeData/PassengerFlow.vue")
            },
            {
                path:'/salesVolume',
                name:'salesVolume',
                meta:{
                    title:'销售额'
                },
                component: () => import (
                    /* webpackChunkName: "dashboard" */
                    "../views/realtimeData/SalesVolume.vue")
            },
            ...marketFlow,
            ...shopPassengerFlow,
            ...marketManagement,
            ...systemManagement,
            {
                path:'/realtimeIndexRanking',
                name:'realtimeIndexRanking',
                meta:{
                    title:'实时指标排行'
                },
                component: () => import (
                    /* webpackChunkName: "dashboard" */
                    "../views/realtimeData/RealtimeIndexRanking.vue")
            },
            {
                path: "/dashboard",
                name: "dashboard",
                meta: {
                    title: '系统首页'
                },
                component: () => import (
                /* webpackChunkName: "dashboard" */
                "../views/Dashboard.vue")
            }, {
                path: "/table",
                name: "basetable",
                meta: {
                    title: '表格'
                },
                component: () => import (
                /* webpackChunkName: "table" */
                "../views/BaseTable.vue")
            }, {
                path: "/charts",
                name: "basecharts",
                meta: {
                    title: '图表'
                },
                component: () => import (
                /* webpackChunkName: "charts" */
                "../views/BaseCharts.vue")
            }, {
                path: "/form",
                name: "baseform",
                meta: {
                    title: '表单'
                },
                component: () => import (
                /* webpackChunkName: "form" */
                "../views/BaseForm.vue")
            }, {
                path: "/tabs",
                name: "tabs",
                meta: {
                    title: 'tab标签'
                },
                component: () => import (
                /* webpackChunkName: "tabs" */
                "../views/Tabs.vue")
            }, {
                path: "/donate",
                name: "donate",
                meta: {
                    title: '鼓励作者'
                },
                component: () => import (
                /* webpackChunkName: "donate" */
                "../views/Donate.vue")
            }, {
                path: "/permission",
                name: "permission",
                meta: {
                    title: '权限管理',
                    permission: true
                },
                component: () => import (
                /* webpackChunkName: "permission" */
                "../views/Permission.vue")
            }, {
                path: "/i18n",
                name: "i18n",
                meta: {
                    title: '国际化语言'
                },
                component: () => import (
                /* webpackChunkName: "i18n" */
                "../views/I18n.vue")
            }, {
                path: "/upload",
                name: "upload",
                meta: {
                    title: '上传插件'
                },
                component: () => import (
                /* webpackChunkName: "upload" */
                "../views/Upload.vue")
            }, {
                path: "/icon",
                name: "icon",
                meta: {
                    title: '自定义图标'
                },
                component: () => import (
                /* webpackChunkName: "icon" */
                "../views/Icon.vue")
            }, {
                path:"/:pathMatch(.*)*",
                name: '404',
                meta: {
                    title: '找不到页面'
                },
                component: () => import (/* webpackChunkName: "404" */
                '../views/404.vue')
            }, {
                path: '/403',
                name: '403',
                meta: {
                    title: '没有权限'
                },
                component: () => import (/* webpackChunkName: "403" */
                '../views/403.vue')
            }
        ]
    }, {
        path: "/login",
        name: "Login",
        meta: {
            title: '登录'
        },
        component: () => import (
        /* webpackChunkName: "login" */
        "../views/Login.vue")
    }
];

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
});

router.beforeEach((to, from, next) => {
    document.title = `${to.meta.title} | vue-manage-system`;
    const role = localStorage.getItem('ms_username');
    if (!role && to.path !== '/login') {
        next('/login');
    } else if (to.meta.permission) {
        // 如果是管理员权限则可进入，这里只是简单的模拟管理员权限而已
        role === 'admin'
            ? next()
            : next('/403');
    } else {
        next();
    }
});

export default router;